.teacher-name-page {
  .heading-title {
    max-width: 750px;
    font-size: 48px;
    line-height: 50px;

    @media (max-width: $mobile480-width) {
      font-size: 30px;
      line-height: 30px;
    }
  }
}

.heading-title {
  @include fontface-bold;
  font-size: 50px;
  line-height: 55.13px;
  color: $white;
  text-transform: uppercase;
  padding: 0;
  margin: 0;
  padding-left: 105px;

  span {
    @include fontface-regular;
    font-size: 40px;
    line-height: 43.21px;
  }

  @media (max-width: $desktop1300-width) {
    padding-left: 50px;
  }

  @media (max-width: $tablet-width) {
    font-size: 30px;
    line-height: 30px;
    padding-left: 30px;

    span {
      font-size: 25px;
      line-height: 30px;
    }
  }
}

.designation-section-title {
  @include fontface-bold;
  padding: 25px 0 25px 105px;
  margin: 50px 0 0;
  font-size: 48px;
  line-height: 50.62px;
  text-transform: uppercase;
  background-color: $white;
  color: $black;

  @media (max-width: $desktop1300-width) {
    padding-left: 50px;
  }

  @media (max-width: $tablet900-width) {
    font-size: 38px;
    line-height: 44px;
  }

  @media (max-width: $mobile480-width) {
    font-size: 26px;
    line-height: 34px;
    padding: 20px;
  }
}
