
.section-heading {
  display: flex;
  align-items: center;
  color: $white;
}

.section-text {
  @include fontface-regular;
  padding: 0;
  margin: 0;
  font-size: 15px;
  line-height: 20px;
  color: $white;

  @media (max-width: $desktop1300-width) {
    font-size: 12px;
  }

  @media (max-width: $tablet-width) {
    font-size: 10px;
    line-height: 12px;
  }
}

.page-title {
  @include fontface-regular;
  padding: 0;
  margin: 0;
  text-transform: uppercase;
  font-size: 48px;
  line-height: 71px;
  margin-bottom: 10px;

  span {
    @include fontface-regular;
    font-size: 64px;
    line-height: 71px;
  }
}

.section-title {
  @include fontface-regular;
  font-size: 96px;
  line-height: 105.84px;
  color: $white;
  padding: 0;
  margin: 0;

  @media (max-width: $desktop1300-width) {
    font-size: 85px;
    line-height: 85.84px;
  }

  @media (max-width: $tablet900-width) {
    font-size: 75px;
  }

  @media (max-width: $tablet-width) {
    font-size: 40px;
    line-height: 55px;
  }

  @media (max-width: $mobile480-width) {
    font-size: 30px;
    line-height: 45px;
  }
}

.section-subtitle {
  @include fontface-regular;
  padding: 0;
  margin: 0;
  font-size: 20px;
  line-height: 24px;
  color: $grey;
  margin-bottom: 10px;

  @media (max-width: $tablet-width) {
    font-size: 15px;
    line-height: 15px;
  }
}

